import React, { FC, useEffect } from 'react'
import { Button, TextField, Link, Typography, Container } from '@material-ui/core'
import { Link as RouterLink } from 'react-router-dom'
import { MoonLoader } from 'react-spinners'
import { yupResolver } from '@hookform/resolvers/yup'
import { useForm, Controller, SubmitHandler } from 'react-hook-form'
import useStyles from './RegisterPage.style'
import FormContainer from '../../../components/Sub/FormContainer/FormContainer'
import { regFormSchema } from '../../../utils/schemas'
import {
  PROJECT_NAME,
  REG_PAGE_DESCRIPTION,
  REG_FORM_EMAIL_PLACEHOLDER,
  REG_FORM_PASS_PLACEHOLDER,
  REG_FORM_PASS_CONFIRM_PLACEHOLDER,
} from '../../../utils/constants'
import SuccesRegistrationPopup from '../../../components/Main/RegisterPage/SuccesRegistrationPopup/SuccesRegistrationPopup'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { signUp } from '../../../services/axios/auth'
import { setUnlogged } from '../../../services/slices/auth-slice'
import processAuthServerError from '../../../utils/auth-server-error'
import { TSignUpData } from '../../../services/types/auth/auth-types'

const RegisterPage: FC = () => {
  const dispatch = useAppDispatch()
  const {
    signUpRequest: { status, isError, errorInfo },
  } = useAppSelector((store) => store.auth)
  const isLoading = status === 'pending'
  const [openSuccessPopup, setOpenSuccessPopup] = React.useState(false)
  const [isRegFormVisible, setIsRegFormVisible] = React.useState(true)

  const openSuccessPopupHandler = () => {
    setIsRegFormVisible(false)
    setOpenSuccessPopup(true)
  }

  const handleCloseSuccessPopup = () => {
    setIsRegFormVisible(true)
    setOpenSuccessPopup(false)
  }

  const {
    control,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm<TSignUpData>({
    resolver: yupResolver(regFormSchema),
    mode: 'onChange',
  })

  useEffect(() => {
    if (isError) processAuthServerError(errorInfo, setError)
  }, [isError])

  const onSubmit: SubmitHandler<TSignUpData> = async (data) => {
    dispatch(setUnlogged())
    try {
      const result = await dispatch(signUp(data)).unwrap()
      if (result.email === data.email) openSuccessPopupHandler()
    } catch {
      return undefined
    }
    return undefined
  }

  const classes = useStyles()

  return (
    <Container className={classes.root}>
      <SuccesRegistrationPopup open={openSuccessPopup} handleClose={handleCloseSuccessPopup} />
      {isRegFormVisible && (
        <FormContainer title={PROJECT_NAME} subtitle="" description={REG_PAGE_DESCRIPTION} error={errors}>
          <form onSubmit={handleSubmit(onSubmit)}>
            <fieldset className={classes.fieldset}>
              <Controller
                name="email"
                control={control}
                render={({ field }) => (
                  <TextField
                    {...field}
                    error={!!errors.email?.message}
                    helperText={errors.email?.message || ' '}
                    type="email"
                    className={`${classes.input} ${classes.firstInput}`}
                    variant="outlined"
                    placeholder={REG_FORM_EMAIL_PLACEHOLDER}
                    inputProps={{ disableUnderline: true }}
                  />
                )}
              />

              <Controller
                name="password"
                control={control}
                render={({ field }) => (
                  <TextField
                    {...field}
                    error={!!errors.password?.message}
                    helperText={errors.password?.message || ' '}
                    type="password"
                    className={classes.input}
                    variant="outlined"
                    placeholder={REG_FORM_PASS_PLACEHOLDER}
                    inputProps={{ disableUnderline: true }}
                  />
                )}
              />
              <Controller
                name="password2"
                control={control}
                render={({ field }) => (
                  <TextField
                    {...field}
                    error={!!errors.password2?.message}
                    helperText={errors.password2?.message || ' '}
                    type="password"
                    className={classes.input}
                    variant="outlined"
                    placeholder={REG_FORM_PASS_CONFIRM_PLACEHOLDER}
                    inputProps={{ disableUnderline: true }}
                  />
                )}
              />
              {isLoading ? (
                <MoonLoader color="#1d6bf3" loading size={39} />
              ) : (
                <Button className={classes.button} type="submit">
                  
                </Button>
              )}
            </fieldset>
          </form>
        </FormContainer>
      )}
      <Typography className={classes.registerTitle} variant="body1">
          ?{' '}
        <Link className={classes.registerLink} component={RouterLink} to="/sign-in" underline="none">
          
        </Link>
      </Typography>
    </Container>
  )
}

export default React.memo(RegisterPage)
