import React, { FC } from 'react'
import Dialog from '@material-ui/core/Dialog'
import { Button, Typography } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'
import DialogContent from '@material-ui/core/DialogContent'
import useStyles from './SuccesRegistrationPopup.styles'
import { PROJECT_NAME, REG_FORM_POPUP_BUTTON, REG_FORM_POPUP_CONFIRM_REGISTRATION } from '../../../../utils/constants'
import IconSuccessRegistration from '../../../../assets/Icons/IconSuccessRegistration/IconSuccessRegistration'

interface ISuccesRegistrationPopupProps {
  open: boolean
  handleClose: () => void
}
const SuccesRegistrationPopup: FC<ISuccesRegistrationPopupProps> = ({ open, handleClose }) => {
  const classes = useStyles()
  const navigate = useNavigate()

  const handleClick = () => {
    navigate('/sign-in')
  }
  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <Typography className={classes.popupHeader} component="h2" variant="h2">
              {PROJECT_NAME}
            </Typography>
            <IconSuccessRegistration />
            <Typography className={classes.popupDescription} component="p" variant="body1">
              {REG_FORM_POPUP_CONFIRM_REGISTRATION}
            </Typography>
            <Button className={classes.popupButton} onClick={handleClick}>
              {REG_FORM_POPUP_BUTTON}
            </Button>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(SuccesRegistrationPopup)
